import React from "react";

export default class Guide extends React.Component {
    constructor() {
        super();
        this.state = {
            imgs: [
                require("@/assets/guide01.jpg"),
                require("@/assets/guide02.jpg"),
                require("@/assets/guide03.jpg"),
                require("@/assets/guide04.jpg")
            ]
        }
    }

    componentDidMount() {
        if (localStorage.visitCount) {
            localStorage.visitCount++;
            if (localStorage.visitCount > 3) {
                this.props.history.push("/index/home");
            }
        } else {
            localStorage.visitCount = 1;
        }
        console.log(localStorage.visitCount)
        var mySwiper = new Swiper('.swiper-container', {
            autoplay: true,
            effect: 'fade',
            fadeEffect: {
                crossFade: true,
            },
            scrollbar: {
                el: '.swiper-scrollbar',
            },
        })
        mySwiper.scrollbar.$dragEl.css('background', '#589de0');
    }

    gotoHome = () => {
        this.props.history.push("/index/home")
    }

    render() {
        return (
            <div style={{ width: "100%", height: "100%" }}>
                <div className="swiper-container" style={{ width: "100%", height: "100%" }}>
                    <div className="swiper-wrapper" style={{ width: "100%", height: "100%" }}>
                        {
                            this.state.imgs.map((img, i) => {
                                return (
                                    <div
                                        className="swiper-slide"
                                        key={i}
                                        style={{
                                            width: "100%",
                                            height: "100%",
                                            position: "relative"
                                        }}
                                    >
                                        <img
                                            src={img}
                                            alt=""
                                            style={{
                                                width: "100%",
                                                height: "100%",
                                                display: "block"
                                            }}
                                        />
                                        <div
                                            style={{
                                                width: "1.3rem",
                                                height: "0.6rem",
                                                borderRadius: "30%",
                                                bacckgroundColor: "rgba(15,15,15,0.5)",
                                                color: "#fff",
                                                fontSize: "0.12rem",
                                                lineHeight: "0.6rem",
                                                textAlign: "center",
                                                position: "fixed",
                                                left: "80%",
                                                top: "1%"
                                            }}
                                            onClick={this.gotoHome}>
                                            跳过引导
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                    <div className="swiper-scrollbar" style={{ marginLft: 0 }}></div>
                </div>
            </div>
        )
    }
}